.rightbar{
    flex:3;
    
}

.birthdayContainer{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.rightWrapper{
    padding: 20px 20px 0 0;
}

.birthdayImg{
    width: 20%;
    height: 20%;
    margin-right: 10px;
}

.birthdayText{

}

.rightbarAd{
    width: 100%;
    border-radius: 15px;
    padding-top: 10px;

}

.rightbarFriendList{
    
    margin: 0;
    list-style: none;
}

.rightbarTitle{
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}

.rightbarInfo{
    margin-bottom: 30px;

}

.rightbarInfoItem{
    margin-bottom: 10px;
}

.rightbarInfoKey{
    font-weight: 500;
    margin-right: 15px;
    color: #555;
}
.rightbarInfoValue{
    font-weight: 300;
}

.rightbarFollowings{
    display: flex;
    flex-wrap: wrap;
    
    justify-content: space-between;
}

.rightbarFollowing{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding: 20px;
    cursor: pointer;
}

.rightbarFollowingImg{
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
}
